<template>
  <div class="box">
    <!-- 最外遮罩层 -->
    <div class="box-pop">
      <!-- 收银界面 -->
      <div class="box-content">
        <!-- 选择支付方式 -->
        <div class="choose-type">
          <p class="choose-type-p">选择支付方式</p>
          <div class="choose-type-div display">
            <div
              v-for="(x, index) in imgList"
              :key="x.key"
              :style="{ borderColor: imgIndex == index ? x.color : '#e6e6e6' }"
              @click="chooseType(index, x.key)"
            >
              <img :src="x.imgSrc" alt="图片加载失败" />
              <p>{{ x.text }}</p>
            </div>
          </div>
        </div>
        <!-- 订单部分 -->
        <div>
          <!-- 订单金额 -->
          <div class="order-amount display">
            <div>订单金额</div>
            <div class="input display">
              <p class="inputValue-p" v-show="inputValue">￥</p>
              <input
                id="input"
                tabindex="1"
                type="text"
                onkeyup="this.value=this.value.match(/\d+\.?\d{0,2}/)"
                onafterpaste="this.value=this.value.match(/\d+\.?\d{0,2}/)"
                placeholder="请输入金额"
                size="1"
                v-model="inputValue"
                :class="{'inputValue-p1':!inputValue}"
                autocomplete="off"
                :maxlength="inputlength"
              />
              <img
                src="../assets/qx.svg"
                alt=""
                class="cncal"
                @click="cancel"
              />
            </div>
          </div>
          <!-- 卡券核销 -->
          <div class="order-amount display">
            <div>卡券核销</div>
            <div class="input display">
              <input
                type="text"
                id="verification"
                placeholder="请输入核销码"
                size="1"
                v-model="couponNo"
                style="margin-left:20px"
                autocomplete="off"
              />
              <div class="dicout display">
                <div @click="verificationFn">
                  <img src="../assets/quma.svg" alt="" />
                </div>
                <button @click="checkCoupon()">确认</button>
              </div>
            </div>
          </div>
          <!-- 各种金额 -->
          <div class="sorts-amount">
            <!-- 应付金额 -->
            <div class="should-amount display">
              <p>应付金额</p>
              <p>￥{{ shouldAmount || "0.00" }}</p>
            </div>
            <!-- 优惠金额 -->
            <div class="should-amount display">
              <p>优惠金额</p>
              <p>￥{{ dicoutAmout || '0.00' }}</p>
            </div>
           
          </div>
        </div>
        <!-- 备注 -->
        <div class="remark display">
          <p>备注</p>
          <input type="text" placeholder="请输入备注" size="1" v-model="remark" autocomplete="off" />
        </div>
        
        
        <!-- 软键盘 -->
        <div class="keyboard display" style="margin-top:40px">
          <div class="keyboard-num noSelect">
            <div
              v-for="(x, index) in numList"
              :key="index"
              :class="{ deepBackground: deepIndex == index + 1 }"
              @click="keyNumber(x, index)"
            >
              {{ x }}
            </div>
          </div>
          <div class="operate">
            <div @click="delet" :class="{ deepBackground: delLight }">
              <img src="../assets/shcnhu.svg" alt="" />
            </div>
            <div
              class="reallybutton noSelect"
              @click="reallyPay"
              style="position: relative"
            >
              确认支付
               
            </div>
            
          </div>
        </div>
        <!-- 支付结果 -->
        <div class="pop-box" v-show="payReaultFlage">
          <!-- <div class="pop-box"> -->
          <div class="pay-result">
            <div class="pay-result-box">
              <div v-show="payReault == 2" class="pay-result-box-div">
            
                <p class="setNumber">{{setNumber}}s</p>
                <img src="../assets/zfcg.svg" alt="" />
                <p>支付成功</p>
              </div>
              <div v-show="payReault == -1" class="pay-result-box-div">
                <img src="../assets/zfsb.svg" alt="" />
                <p>支付失败</p>
              </div>
              <!-- 订单编号 -->
              <div class="order-number display">
                <p>
                  订单编号<span>{{ tradeOrderId }}</span>
                  
                </p>
                <img
                  src="../assets/fuzhi.svg"
                  alt=""
                  v-clipboard:copy="tradeOrderId"
                  v-clipboard:success="onCopy"
                   v-clipboard:error="onError"
                  v-show="tradeOrderId"
                />
                

              </div>
              <div class="close" @click="closeUp">关闭</div>
            </div>
          </div>
        </div>
        <!--各种支付 -->
        <div class="pay-code" v-show="payTypeFlage&&payKey != 'payFace'">
          <!-- 付款码支付 -->
          <div class="pay-code-div" v-show="payKey == 'payCode'">
            <div>
              <div class="display">
                <img src="../assets/fk.svg" />
                <p class="onltP">支付中…</p>
              </div>
              <p class="pay-code-p">{{codeFlase?'已扫码等待付款':'请扫描付款码收款'}}</p>
            </div>
            <div @click="cancal">取消收款</div>
          </div>
          <!-- 二维码码支付 -->   
          <div class="pay-code-div" v-show="payKey == 'scanCode'" style="background:white" >
            <div style="height:auto;background:white">
              <div class="display code_img_url" v-show="codeImgUrl">
                <img :src="codeImgUrl" />
              </div>
              <div class="display" v-if="payKey == 'scanCode' && !codeImgUrl" :d="codeImgUrlFun()" style="height:174px">
                
                <div style="margin:0 auto;margin-bottom:0;background:white;height:100%">
                  <img src="../assets/sm.svg" alt="" style="width:40px;height:40px;">
                  <p>获取二维码失败，请重试</p>
                </div>
              </div>
              <div class="twoCode display" v-show="codeImgUrl">
                
                 <img src="../assets/alipay.svg" class="svg" alt="">
                 <img src="../assets/wechatpay.svg" class="svg" alt="">
              </div>
              <p class="pay-code-p" style="background:white;margin:0px;padding-top:10px" v-show="codeImgUrl">请扫描二维码收款</p>
            </div>
            <!-- <div> -->
            <div style="margin:0" @click="cancal">取消收款</div>
             
          </div>
          <!-- 现金码支付 -->
          <div class="pay-cash" v-show="payKey == 'payCash'">
            <div class="pay-cash-really">
              <img src="../assets/xj.svg" alt="" />
              <p>现金支付确认</p>
            </div>
            <div class="should-amount display">
              <p>实付金额</p>
               
                <p style="width:auto;height:auto;margin:0;line-height:auto">
                  ￥
                </p>
                <input
                  class="amountPaid-input"
                  id="input_should"
                  tabindex="1"
                  type="text"
                  onkeyup="this.value=this.value.match(/\d+\.?\d{0,2}/)"
                  onafterpaste="this.value=this.value.match(/\d+\.?\d{0,2}/)"
                  placeholder="请输入金额"
                  size="1"
                  v-model="amountPaid"
                  style="width:100px;"
                  autocomplete="off"
                />
              
                
          
             
              <!-- <p>￥{{ !shouldAmount ? "0.00" : amountPaid }}</p> -->
            </div>
            <div class="should-amount display">
              <p>找零金额</p>
              <p  style="text-align:left;"><span style="margin-right:10px">￥</span>{{ changeAmount }}</p>
            </div>
            <div class="really-button display">
              <button @click="cancal">取消</button>
              <button @click="cashClicOkFn()">确认</button>
            </div>
          
          </div>
          <!-- 会员支付-->
          <div class="pay-member" v-show="payKey == 'payMember'">
            <div style="background: #f3f3f3; overflow: hidden;">
              <div class="pay-member-title display">
                <img src="../assets/hy.svg" alt="" />
                <p>会员支付确认</p>
              </div>
              <div class="pay-member-card display" style="paddingBottom:10px">
                <div>
                  <input
                    type="text"
                    size="1"
                    id="memberPay"
                    v-model="memberPhneCard"
                    v-show="!memberPayTypeCode"
                    placeholder="搜索会员手机号/卡号" 
                    @keyup.enter="eneter"
                    autocomplete="off"
                  />
                  <input
                    type="text"
                    size="1"
                    id="inpin"
                    v-model="memberPayTypeCode"
                    v-show="memberPayTypeCode"
                    placeholder="扫描付款码"
                    @keyup.enter="eneter1"
                    autocomplete="off"
                  />
                </div>
                
                  <div @click="open">
                    <img src="../assets/quma.svg" alt="" />
                    <P>付款码</P>
                  </div>
                
              </div>
              <div class="pay-member-mess" v-show="getAllMemberInfo">
                <div class="display pay-member-mess-name">
                  <p>姓名：<span>{{getAllMemberInfo.memberName}}</span></p>
                </div>
                <div>
                  <p>卡号：<span>{{getAllMemberInfo.memberNo}}</span></p>
                </div>
                <div>
                  <p>电话：<span>{{getAllMemberInfo.tel}}</span></p>
                </div>
                <div>
                  <p>余额：<span>￥{{getAllMemberInfo.balance}}</span></p>
                </div>
                <div>
                  <p v-show="(getAllMemberInfo.balance)-amountPaid>=0">交易后余额：<span>￥{{(getAllMemberInfo.balance)-amountPaid}}</span></p>
                </div>
              </div>
            </div>
            <div class="really-button1 display">
              <button @click="cancal">取消</button>
              <button @click="payreally">确认</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { set } from 'vue/types/umd';
export default {
  data() {
    return {
      imgList: [
        {
          imgSrc: require("../assets/fk.svg"),
          text: "付款码支付",
          color: "#17c2b2",
          key: "payCode",
        },
        {
          imgSrc: require("../assets/sm.svg"),
          text: "扫码支付",
          color: "#000000",
          key: "scanCode",
        },
        {
          imgSrc: require("../assets/sl.svg"),
          text: "刷脸支付",
          color: "#4784ff ",
          key: "payFace",
        },
        {
          imgSrc: require("../assets/xj.svg"),
          text: "现金支付",
          color: "#ffb73b",
          key: "payCash",
        },
        {
          imgSrc: require("../assets/hy.svg"),
          text: "会员支付",
          color: "#d19710",
          key: "payMember",
        },
      ], //支付方式集合
      numList: ["1", "2", "3", "4", "5", "6", "7", "8", "9", ".", "0", "00"], //键盘数字集合
      deepIndex: "", //软键盘数字高亮索引
      imgIndex: "", //付款方式框高亮索引
      time: "", //计时器
      delLight: false, //删除高亮
      inputValue: "", //订单金额
      inputitem: "", //订单中间状态
      dicoutAmout: "0.00", //优惠金额
      amountPaid: "", //实付金额
      shouldAmount: "0.00", //应付金额
      changeAmount: "0.00", //找零金额
      key: "payCode", //支付标识
      payKey: "", //支付弹窗开关
      payTypeFlage: false, //支付类型弹窗开关
      codeValue: "", //扫码值
      tradeOrderId: "", //订单编号
      payReault: "", //支付结果
      payReaultFlage: false,
      focusBoolan: false,
      codeImgUrl: "", // 支付二维码
      couponNo: "", //核销码
      verificationState: false, // 核销码鷌抢状态
      memberPhneCard:"",//会员手机号(卡号)
      getAllMemberInfo:"",//会员个人信息
      memberPayTypeCode:"",//会员付款码
      memberCodeFlage:false,//会员付款码开关
      a:"",
      couponState:true,
      setNumber:5,//支付结果倒计时
      inputFocus:"",//输入框聚焦
      codeFlase:"",//code开关
      remark:"",//备注
      inputlength:7
    };
  },
  created() {
     
    
    this.amountPaid = "0.00";
    // 确认按钮添加键盘事件
    let self = this;
    document.addEventListener("keyup", () => {
      let key = window.event.keyCode;
      if (key == 13 && !self.verificationState){
        self.reallyPay();
      }
    });
    // this.onkeydown()
    
  },
  mounted() {
    this.inputFocus = document.getElementById("input");
    this.inputFocus.focus();
    this.a=document.getElementById("inpin");
  },
  watch: {
    //   监听核销码
    couponNo(vla, old) {
      if (parseFloat(vla) < parseFloat(old)) {
        this.dicoutAmout = "0.00";
      } 
    },
    // 监听payKey 更改 codeImgUr制空
    payKey() {
      this.codeImgUrl = null;
    },
    //监听订单金额 改变实付金额
    inputValue(value) {
     
     
      if (value) {
        let index=value.indexOf(".")
        // console.log(index)
         if(index>0){
          this.inputlength=10
          value=value.substr(0,index+3)

         }
        // let that=this
        this.shouldAmount = value;
        this.amountPaid = this.shouldAmount
        
      } else {
        this.shouldAmount = "";
        this.couponNo = "";
        this.dicoutAmout = "0.00";
      }
    },
    // 监听实付金额 改变应付金额
    // shouldAmount(value) {
    //     this.dicoutAmout = parseFloat(this.dicoutAmout);
    //   value = parseFloat(value);
    //   if (value) {
    //     if ((value-parseFloat(this.dicoutAmout)>0)) {
    //       this.amountPaid = this.accSub(value, parseFloat(this.dicoutAmout));
    //     } else {
    //       this.amountPaid = "0.01";
    //     }
    //   } else {
    //     this.amountPaid = "0.00";
    //   }
    // },
    // 监听优惠金额的变化
    dicoutAmout(val) {
      if(val&&val!="0.00"){
        if((parseFloat(this.inputValue)-parseFloat(this.dicoutAmout)>0)){
          
          this.shouldAmount = this.accSub(this.inputValue, val);
          this.amountPaid = this.shouldAmount
        }else{
          this.amountPaid ="0.01";
          this.shouldAmount ="0.01";
        }
      }
     
    },
    // 监听实付金额
    amountPaid(val) {
      //   if (parseFloat(val) > parseFloat(old)) {
      if (this.payKey == "payCash") {
        let a =parseFloat(val) -parseFloat(this.shouldAmount)
        this.changeAmount = a > 0 ? a : "0.00";
      }
      //   }
    },
    
    // 监听扫码值变化 发起请求上游数据
    codeValue(value) {
      console.log("扫码值",value)
      let that = this;

      if (!this.amountPaid) {
        return;
      }

     
      console.log(this.codeFlase)
      if(value){
        this.codeFlase=true
        this.$axios
        .memberPay({
          requiredAmount: this.shouldAmount,
          realAmount: this.amountPaid,
          barCode: value,
          remark:this.remark
        })
        .then((res) => {
          console.log(res);
          this.tradeOrderId = res.tradeOrderId;
          if (res.orderStatus == 1) {
            //订单正在处理中 2s查询一次订单状态
            this.time = setInterval(() => {
              that.referStatus(res.tradeOrderId);
            }, 2000);
          } else if (res.orderStatus == 2) {
            this.payReault = res.orderStatus;
            this.settimeout()
          }
        });
      }
      
    },
    // 监听会员手机号(卡号)变化
    memberPhneCard(){
      this.getAllMemberInfo=""
    },
    // 监听付款码值变化
    memberPayTypeCode(){
      // console.log(this.a)
      // this.a.focus()
    }
  },

  methods: {
    // 复制成功消息提示
    onCopy(){
      this.messageTip("复制成功","success")
    },
    // 复制失败消息提示
    onError(){
       this.$message.error('复制失败');
    },
    // 支付结果点击关闭事件
    closeUp(){
     this.empty()
    },
   //会员支付查询个人信息(手机号\卡号)
    eneter(){
      this.getMemberMess(2,this.memberPhneCard)
    },
    // 开启会员付款码扫描
    open(){
      this.memberCodeFlage=true
      this.memberPayCode=""
      this.memberPhneCard=""
      this.a.focus()
      this.onkeydown() 
       
      this.messageTip('请扫描付款码','info')
    },
    // 消息提示
    messageTip(mess,type){
      this.$message({
          message: mess,
          duration:2000,
          type:type
        });
    },
    // 请求会员个人信息
    getMemberMess(type,memberId){
      this.$axios.getAllMemberInfo({type:type,memberId:memberId}).then(res=>{
        res=JSON.stringify(res)
        if(res==="{}"){
          this.getAllMemberInfo=""
        }else{
          this.getAllMemberInfo=JSON.parse(res)
          document.onkeydown = () => {};
          this.memberCodeFlage=false
        }
        
       
      }).catch(err=>{
       this.$message(err.msg);
       this.getAllMemberInfo=""
      })
    
    },
    // 开启设备监听
    onkeydown() {
      // 获取扫码枪或扫码盒值
      var b = "";
      var that = this;
      that.codeValue = "";
      document.onkeydown = () => {
        if (event.keyCode != 13) {
         
          var bizCode = String.fromCharCode(event.keyCode);
          if (event.keyCode >= 48 && event.keyCode <= 122) {
            b = b + bizCode;
          }
        } else {
          console.log(that.payKey == "payMember",that.memberCodeFlage,that.payKey,that.key)
          if (this.verificationState) {
            setTimeout(()=>{
              that.verificationState = false;
            },0)
            
            console.log("3422222243");
            // that.checkCoupon();
          } else if (that.payTypeFlage) {
             b=JSON.parse(JSON.stringify(b));
              if(that.payKey == "payCode"){
                that.codeValue = b
              }else if(that.payKey == "payMember"&&that.memberCodeFlage){
                that.memberPayTypeCode=b
                this.getMemberMess(3,JSON.parse(JSON.stringify(b)))
              }
            
            
          }
          b = "";
        }
      };
    },
    // 付款方式输入框
    chooseType(index, key) {
      this.imgIndex = index;
      this.key = key;
      this.tradeOrderId=""
      // console.log(this.key)
    },
    // 数字软键盘输入
    keyNumber(item, index) {
      this.deepIndex = index + 1;
      this.inputitem += item;
      this.inputValue = this.inputitem.match(/\d+\.?\d{0,2}/).toString();

      setTimeout(() => {
        this.deepIndex = "";
      }, 400);
    },
    //取消软件盘输入
    cancel() {
      this.inputValue = "";
      this.inputitem = "";
    },
    // 删除软键盘输入值
    delet() {
      // let inputValue =this.inputValue
      if (this.inputValue.length > 0) {
        this.inputitem=this.inputitem.substr(0, this.inputValue.length - 1);
        this.inputValue = this.inputValue.substr(0, this.inputValue.length - 1);
        this.delLight = true;
        setTimeout(() => {
          this.delLight = false;
        }, 400);
      } else {
        this.inputValue = "";
      }

      // console.log(typeof(this.inputValue),this.inputValue.length,1)
    },
    // 确认支付
    reallyPay() {
      let input = document.getElementById("input");
      input.blur();
      if(this.key == "payFace"){
        this.$message('刷脸支付暂未开通，敬请期待');
      }
      if (this.key == "payCode") {
        this.onkeydown();
      }else if(this.key == "payMember"){
        document.getElementById('memberPay').focus()
      }
       
      if (this.inputValue) {
        this.focusBoolan = true;
        this.payKey = this.key;
        this.payKey && (this.payTypeFlage = true);
      }
    },
    // 取消付款
    cancal() {
      // this.inputValue = "";
      // this.inputitem = "";
      // this.payKey = "";
      // this.memberCodeFlage=false
      // this.codeFlase=""
      // this.changeAmount="0.00"
      // this.inputFocus.focus()
      // if(this.memberPayTypeCode||this.memberPhneCard){
      //   this.memberPayTypeCode=""
      //   this.memberPhneCard=""
      //   this.getAllMemberInfo=""
      // }
      // this.payTypeFlage = false;
      this.empty()
      this.time && clearInterval(this.time);
    },
    // 查询订单状态
    referStatusCode(tradeOrderId) {
      this.time = setInterval(() => {
        this.referStatus(tradeOrderId);
      }, 2000);
    },
    // 查询订单状态
    referStatus(tradeOrderId) {
      let that = this;
      this.$axios.queryStatus({ tradeOrderId: tradeOrderId }).then((res) => {
        that.payReault = res.orderStatus;
        console.log(res);
        if (res.orderStatus == 2 || res.orderStatus == -1) {
          clearInterval(this.time);
          this.settimeout()
          

        }
      });
    },
    //  获取二维码图片
    codeImgUrlFun() {
      console.log(this.amountPaid)
      let payAmount = this.amountPaid;
      if (payAmount == "0.00") return;
      let that = this;
      this.$axios
        .viewCodeAmount({
          payAmount,
        })
        .then((res) => {
          this.codeImgUrl = res.codeImgUrl;
          //   this.tradeOrderId = res.tradeOrderId;
          that.time = setInterval(() => {
            that.referStatus(res.tradeOrderId);
          }, 2000);
        });
    },
    //  卡卷核销扫码
    verificationFn() {
      
      this.messageTip('请扫描核销码','info')
      this.couponNo = "";
      this.verificationState = true;
      document.getElementById("verification").focus();
      this.onkeydown();
    },
    // 核销码核销
    checkCoupon() {
      let { couponNo, shouldAmount: amount,couponState } = this;
      this.couponState = !couponState
      if(!couponState){
        return
      }
      if(couponNo.length){
        this.$axios
        .checkCouponPost({
          couponNo,
          amount,
        })
        .then((res) => {
          console.log(res)
          this.dicoutAmout=(res.couponAmount/100)
          // this.dicoutAmout = "5.00";
          this.verificationState = false;
          document.onkeydown = () => {};
          //  更改优惠金额
          this.messageTip('核销成功','success')
           
          this.couponState=false
        })
        .catch((err) => {
          console.log("err", err);
          this.$message.error(err.msg);
          this.verificationState = false;
        });
      }
      
    },
    // 现金支付
    cashClicOkFn() {
      let { amountPaid: requiredAmount, shouldAmount: realAmount } = this;
      this.$axios
        .cash({
          requiredAmount,
          realAmount,
          remark:this.remark
        })
        .then((res) => {
          this.tradeOrderId = res?.tradeOrderId ?? "";
          if (res.orderStatus == 2) {
              this.referStatus(res.tradeOrderId);
            
          }
        });
    },

    accept(text) {
      alert("Input text: " + text);
      this.hide();
    },

    show(e) {
      this.input = e.target;
      this.layout = e.target.dataset.layout;

      if (!this.visible) this.visible = true;
    },

    hide() {
      this.visible = false;
    },
    // 获取输入框
    inputShouldFn() {
      return document.getElementById("input_should");
    },
    // 会员卡余额发起请求
    payreally(){
      if(this.memberPhneCard&&!this.getAllMemberInfo){
        this.getMemberMess(2,this.memberPhneCard)
      }else if(this.getAllMemberInfo){
         this.$axios.memberPayBalance({
          requiredAmount:this.inputValue,
          realAmount:this.inputValue,
          memberNo:this.memberPhneCard,
          memberPayCode:this.memberPayTypeCode,
          remark:this.remark
        }).then(res=>{
          console.log(res)
          res=JSON.stringify(res)
          if(res==="{}"){
            this.payReault =-1;
          }else{
            this.payReault =JSON.parse(res).orderStatus;
            this.tradeOrderId =JSON.parse(res).tradeOrderId;
          }
          this.settimeout()
        }).catch(err=>{
          this.$message.error(err.msg);
          
        })
      }
     
    },
    // 关闭支付结果弹窗
    settimeout(){
      let that=this
      that.payKey = "";
      that.payTypeFlage = false;
      that.payReaultFlage = true;
      let time=setInterval(()=>{
        that.setNumber-=1
       
      },1000)
      setTimeout(() => {
        clearInterval(time)
         // 关闭支付结果弹窗
        that.empty()
       
        
      }, 5000);
    },
    // 关闭弹窗 并清空值
    empty(){
      let that=this
      that.payReaultFlage = false;
      that.payReault = "";
      that.inputValue = "";
      that.inputitem = "";
      that.couponNo=""
      that.setNumber=5
      that.codeFlase=""
      that.changeAmount="0.00"
      that.remark=""
      that.inputFocus.focus()
      that.payKey = "";
      that.memberCodeFlage=false
      that.payTypeFlage = false;
      if(that.memberPayTypeCode||that.memberPhneCard){
        that.memberPayTypeCode=""
        that.memberPhneCard=""
        that.getAllMemberInfo=""
      }
    },
    // 浮点小数精度函数
    accSub(arg1, arg2) {
      var r1, r2, m, n;
      try {
        r1 = arg1.toString().split(".")[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split(".")[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2)); //last modify by deeka //动态控制精度长度
      n = r1 >= r2 ? r1 : r2;
      return ((arg1 * m - arg2 * m) / m).toFixed(n);
    },
    
  },
};
</script>

