import axios from "./axios"
import apiPathList from "./apiPath"
import apiPathHfList from "./hfApi{ath"


class apiconfig {
    constructor() {
        this._init()
    }
    apiPath = [
        ...apiPathList,
        ...apiPathHfList,//会峰定义的接口内容
    ]
    _apiconfig = {}
    _init() { // 初始化工具

        this.apiPath.forEach(item => {
            if (!item[2]) {
                this.apiPath.forEach(item => {
                    this.$apiconfig[item[0]] = () => axios.get(item[0])
                })
                return
            }
            this._apiconfig[item[0]] = (data) => {
                if (data?.url) { // 添加URL形式的参数
                    data.url.forEach(item => {
                        item[1] += "/" + item
                    })
                    delete data.url
                }
                if (item[2] == "GET" || !item[2]) { // 处理GET的特殊性
                    return axios[item[2].toLowerCase()](item[1], { params: { ...data } })
                } else {
                    return axios[item[2].toLowerCase()](item[1], { ...data })
                }
            }
        })
    }
}

export default new apiconfig