
import axios from 'axios';
// import config from "../config";


let setTimeoutTiemId = ""
axios.defaults.baseURL ='https://www.juhezhifu.com/api';
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
axios.interceptors.request.use(config => {
	let layuiAdmin = localStorage.getItem("layuiAdmin");
	let Authorization = JSON.parse(layuiAdmin).Authorization || "";
	// let Authorization = JSON.parse(layuiAdmin) && JSON.parse(layuiAdmin).Authorization || "";
	Authorization && (config.headers.authorization = Authorization);
	config.headers.mpVersion = '0.0.0'
	return config;
}, error => {
	return Promise.reject(error);
});
// 请求处理
axios.defaults.transformRequest = function (data) {
	if (!data) return data;
	let result = ``;
	for (let attr in data) {
		// if (!data.hasOwnProperty(attr)) break;
		result += `&${attr}=${data[attr]}`;
	}


	return result.substring(1);
};

axios.defaults.validateStatus = function (status) {
	return /^(2|3)\d{2}$/.test(status);
}
//请求拦截器
axios.defaults.withCredentials = true;
axios.interceptors.response.use(response => {
	clearTimeout(setTimeoutTiemId)
	if (response.data.code !=0) {
		return Promise.reject(response.data);
	}
	return response?.data?.data ?? {};

}, error => {
	clearTimeout(setTimeoutTiemId)
	if (error.response) {
		switch (error.response.status) {
			case 401:
				alert("401")
				break;
			/* if (error.response.config.url == "/api/current/myData") {
				return Promise.reject(error);
			}
			window.location.href = config.redirection // "https://h5.jieliuwudao.com"
			localStorage.removeItem('iToken')
			return Promise.reject(error); */
			case 403:
				alert("403")
				break;
			case 404:
				alert("404")
				break;
			default:
				alert("请求出错")
				// window.$alert("服务器报错信息："+error.response.data.msg);
				break;
		}
	} else {
		if (!window.navigator.onLine) {
			return;
		}
		return Promise.reject(error);
	}
});
export default axios;